<template>
  <transition name="fade" mode="in-out">
    <div class='seeOceanPage'>
        <v-Header class="head">
          <span slot='text'>看海洋</span>
        </v-Header>
        <nav>
            <ul :class='["menu",showMenu&&"show"]'>
                <li class="active">潜水</li>
                <li>冲浪</li>
                <li>游艇</li>
                <li>帆船</li>
                <li>水下摄影</li>
                <li>菜鸟攻略</li>
                <li>大卡分享</li>
            </ul>
            <div class='showMenuBtn' @click="showMenuFun"><div :class="['btn',showMenu&&'show']"><span class="iconfont icon-arrow-top"></span></div></div>
        </nav>
        <div class="articleListsCon" v-for='articleInfo in articleLists' :key='articleInfo.id'>
          <v-article :articleInfo='articleInfo' />
        </div>
    </div>
  </transition>
</template>
<script>
import Header from '../../components/header';
import ArticleInfo from '../../components/seeOcean/articleInfo';

 export default {
    name: '',
    components: {'v-Header':Header,'v-article':ArticleInfo},
    props: {},
    data () {
      return {
          showMenu:false,
          loading:false,
          articleLists:[
            { 
              href:'/articleDetails/321',
              articleImg: 'https://s33.postimg.cc/acjanzcm7/image.jpg',
              articleTitle: '我得想一个好拉轰的标题，比如说速看速看！马上就要被删了！有一只小熊在潜水！',
              articleTime: '号角海洋文化 07-25'
            },
            {
              href:'/articleDetails/3214458',
              articleImg: 'https://s33.postimg.cc/fp851yvbz/image.jpg',
              articleTitle: '标题这种东西，不是越长越好的好吗？',
              articleTime: '号角海洋文化 07-25'
            },
            {
              href:'#',
              articleImg: 'https://s33.postimg.cc/acjanzcm7/image.jpg',
              articleTitle: '我得想一个好拉轰的标题，比如说速看速看！马上就要被删了！有一只小熊在潜水！',
              articleTime: '号角海洋文化 07-25'
            },
            {
              href:'#',
              articleImg: 'https://s33.postimg.cc/acjanzcm7/image.jpg',
              articleTitle: '我得想一个好拉轰的标题，比如说速看速看！马上就要被删了！有一只小熊在潜水！',
              articleTime: '号角海洋文化 07-25'
            }
          ]
      }
    },
    watch: {},
    computed: {},
    created () {
    },
    methods: {
      showMenuFun () {
          this.showMenu=!this.showMenu;
      },
      initData () {}
    }
  }
</script>
<style lang="scss" scoped>
*{
  -webkit-tap-highlight-color: rgba(0,0,0,0);
      -webkit-tap-highlight-color: transparent;
      border: none;
      margin: 0;
      padding: 0;
}
.seeOceanPage{
  background:white;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  overflow-x: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
nav{
    width: 100%;
    overflow-x:hidden;
    height: auto;
    border-bottom: 0.01rem solid rgb(229,229,229); 
    position: relative;
    .menu{
        width: 7.9rem;
        font-family: 'Microsoft YaHei';
        font-size: 0.3rem;
        height: 0.87rem;
        margin: 0 0 0 0.3rem;
        overflow: hidden;
        transition: height 0.4s;
        &.show{
            width: 6.9rem;
            margin: auto;
            height: 1.45rem;
        }
        li{
            float: left;
            margin: 0.3rem 0.5rem -0.3rem 0;
            color: #666666;
            &.active{
                color: #1797c6;
            }
        }
    }
    .showMenuBtn{
        width: 0.86rem;
        height: 0.56rem;
        position: absolute;
        top: 0.3rem;
        right: 0;
        background:rgba(255,255,255,1);
        text-align:center;
        color:rgb(102,102,102);
        line-height: 0.3rem;
        .btn{
          align-items: center;
          justify-content: center;
          display: flex;
          width:100%;
          height:100%;
          transform: rotate(180deg);
          transition:transform 0.5s;
          &.show{
            transform: rotate(0deg);
          }
        .iconfont{
            font-size: 0.28rem;
        }
        }
    }
}
    .articleListsCon{
        font-family: 'Microsoft YaHei';
        width: 100%;
        height: auto;
        margin: 0 auto;
        border-bottom: 0.01rem solid rgb(229,229,229); 
    }
</style>





