<template>
  <div class='articleInfo' @click='changeHref(articleInfo.href)'>
      <div class="articleImg" :style="{backgroundImage:'url('+articleInfo.articleImg+')'}"></div>
      <div class='articleTitle' v-text='articleInfo.articleTitle'></div>
      <div class='articleTime'  v-text='articleInfo.articleTime'></div>
  </div>
</template>
<script>
 export default {
    name: '',
    components: {},
    props: ['articleInfo'],
    data () {
      return {}
    },
    watch: {},
    computed: {},
    created () {
    },
    methods: {
      showMenuFun () {
          this.showMenu=!this.showMenu;
      },
      changeHref(_href){
        this.$router.push(_href)
      },
      initData () {}
    }
  }
</script>
<style lang="scss" scoped>
*{
      border: none;
      margin: 0;
      padding: 0;
    }
  .articleInfo{
      width: 6.9rem;
      margin: 0.3rem auto 0;
      .articleImg{
          width: 6.9rem;
          height: 3.9rem;
          background-position: center center;
          background-size: cover;
      }
      .articleTitle{
          line-height: 0.54rem;
          margin: 0.12rem 0;
          font-size: 0.3rem;
          color:#333333;
      }
      .articleTime{
          font-size: 0.22rem;
          color:#888888;
          margin-bottom:0.3rem;
      }
  }
</style>





